export default {
  layout: "Profile-content",
  mounted() {
    this.getsocial();

  },
  data() {
    return {
      socials: '',
      social: {
        type: "",
        name: '',
      },
      errors: {},
    };
  },
  methods: {
    getsocial() {
      this.$axios
        .$get("/social/show")
        .then((res) => {
          this.socials = res;
        })
        .catch((err) => {
          console.log(err);
        });
    },
    socialEdit(id) {
      $("#socialUpdate").modal("show");
      this.$axios
        .$get("/social/edit/" + id)
        .then((res) => {
          this.social = res;
        })
        .catch((err) => {
          console.log(err);
        });
    },
    socialUpdate() {
      this.$axios
        .$post("/social/update/" + this.social.id, this.social)
        .then((res) => {
          this.getsocial();
          $("#socialUpdate").modal("hide");
          this.$toaster.success(res.message);
          this.errors = "";


        })
        .catch((err) => {
          console.log(err);
          this.errors = err.response.data.errors;
        });
    },
    socialDelete(id) {
      if (confirm("Are you sure to delete this social?")) {
        this.$axios
          .$get("/social/delete/" + id)
          .then((res) => {
            this.getsocial();
            this.$toaster.error(res.message);
          })
          .catch((err) => {
            console.log(err);
          });
      }
    },
    





  },
};
